// src/component/PostFormWithUpload.tsx
"use client";

import { useState, useTransition } from "react";
import { createPost } from "@/actions/createPost";
import { MoimPostCat } from "@prisma/client";
import { useRouter } from "next/navigation";

type Props = {
  activityId: string;
};

export default function PostFormWithUpload({ activityId }: Props) {
  const router = useRouter();
  const [isPending, startTransition] = useTransition();

  // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Í¥ÄÎ†® ÏÉÅÌÉú
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [preview, setPreview] = useState("");
  const [uploading, setUploading] = useState(false);
  const [uploadedUrl, setUploadedUrl] = useState("");
  const [error, setError] = useState("");
  const [formError, setFormError] = useState("");

  // ÌååÏùº ÏÑ†ÌÉù Ìï∏Îì§Îü¨
  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];

    if (!file) {
      return;
    }

    // ÌååÏùº ÌÅ¨Í∏∞ Ï≤¥ÌÅ¨ (5MB Ï†úÌïú)
    const maxSize = 5 * 1024 * 1024;
    if (file.size > maxSize) {
      setError("ÌååÏùº ÌÅ¨Í∏∞Îäî 5MB Ïù¥ÌïòÏó¨Ïïº Ìï©ÎãàÎã§");
      return;
    }

    // ÌååÏùº ÌÉÄÏûÖ Ï≤¥ÌÅ¨ (Ïù¥ÎØ∏ÏßÄÎßå ÌóàÏö©)
    if (!file.type.startsWith("image/")) {
      setError("Ïù¥ÎØ∏ÏßÄ ÌååÏùºÎßå ÏóÖÎ°úÎìúÌï† Ïàò ÏûàÏäµÎãàÎã§");
      return;
    }

    setError("");
    setSelectedFile(file);

    // ÎØ∏Î¶¨Î≥¥Í∏∞ ÏÉùÏÑ±
    const reader = new FileReader();
    reader.onloadend = () => {
      setPreview(reader.result as string);
    };
    reader.readAsDataURL(file);
  };

  // Ïù¥ÎØ∏ÏßÄ ÏÑ†ÌÉù Ï¥àÍ∏∞Ìôî
  const handleReset = () => {
    setSelectedFile(null);
    setPreview("");
    setUploadedUrl("");
    setError("");
  };

  // Ìèº Ï†úÏ∂ú Ìï∏Îì§Îü¨
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFormError("");

    const form = e.currentTarget;
    const formData = new FormData(form);

    // Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏÑ†ÌÉùÎêòÏóàÏßÄÎßå ÏïÑÏßÅ ÏóÖÎ°úÎìúÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞
    if (selectedFile && !uploadedUrl) {
      setUploading(true);
      setError("");

      try {
        const uploadFormData = new FormData();
        uploadFormData.append("file", selectedFile);

        const response = await fetch("/api/upload", {
          method: "POST",
          body: uploadFormData,
        });

        if (!response.ok) {
          throw new Error("ÏóÖÎ°úÎìú Ïã§Ìå®");
        }

        const data = await response.json();
        setUploadedUrl(data.url);

        // ÏóÖÎ°úÎìúÎêú Ïù¥ÎØ∏ÏßÄ URLÏùÑ Ìèº Îç∞Ïù¥ÌÑ∞Ïóê Ï∂îÍ∞Ä
        formData.append("imageUrl", data.url);
      } catch (err) {
        setError("Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§");
        console.error(err);
        setUploading(false);
        return;
      } finally {
        setUploading(false);
      }
    } else if (uploadedUrl) {
      // Ïù¥ÎØ∏ ÏóÖÎ°úÎìúÎêú Í≤ΩÏö∞
      formData.append("imageUrl", uploadedUrl);
    }

    // Server Action Ìò∏Ï∂ú
    startTransition(async () => {
      try {
        const result = await createPost(activityId, null, formData);

        if (result?.error) {
          setFormError(result.error);
        }
        // redirectÎäî createPost ÎÇ¥Î∂ÄÏóêÏÑú Ï≤òÎ¶¨Îê®
      } catch (err) {
        console.error(err);
        setFormError("Í≤åÏãúÍ∏Ä ÏûëÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§");
      }
    });
  };

  return (
    <div className="max-w-2xl mx-auto p-6">
      <h1 className="text-2xl font-bold mb-6">Í≤åÏãúÍ∏Ä ÏûëÏÑ±</h1>

      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÑ†ÌÉù */}
        <div>
          <label className="block text-sm font-medium mb-2">Ïπ¥ÌÖåÍ≥†Î¶¨</label>
          <select
            name="category"
            className="w-full px-4 py-2 border rounded-lg"
            required
          >
            <option value="">ÏÑ†ÌÉùÌïòÏÑ∏Ïöî</option>
            <option value={MoimPostCat.Î™®ÏûÑÌõÑÍ∏∞}>üì∏ Î™®ÏûÑÌõÑÍ∏∞</option>
            <option value={MoimPostCat.Í∞ÄÏûÖÏù∏ÏÇ¨}>üëã Í∞ÄÏûÖÏù∏ÏÇ¨</option>
            <option value={MoimPostCat.ÏûêÏú†}>üí¨ ÏûêÏú†</option>
            <option value={MoimPostCat.Í≥µÏßÄ}>üì¢ Í≥µÏßÄ</option>
          </select>
        </div>

        {/* Ï†úÎ™© */}
        <div>
          <label className="block text-sm font-medium mb-2">Ï†úÎ™©</label>
          <input
            type="text"
            name="title"
            className="w-full px-4 py-2 border rounded-lg"
            required
          />
        </div>

        {/* Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú ÏòÅÏó≠ */}
        <div>
          <label className="block text-sm font-medium mb-2">
            Ïù¥ÎØ∏ÏßÄ Ï≤®Î∂Ä (ÏÑ†ÌÉù)
          </label>
          <div className="border-2 border-dashed border-gray-300 rounded-lg p-6">
            {!preview ? (
              // ÌååÏùº ÏÑ†ÌÉù Ï†Ñ
              <div className="text-center">
                <svg
                  className="mx-auto h-12 w-12 text-gray-400 mb-4"
                  stroke="currentColor"
                  fill="none"
                  viewBox="0 0 48 48"
                >
                  <path
                    d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>

                <label className="cursor-pointer">
                  <span className="text-blue-500 hover:text-blue-600 font-medium">
                    ÌååÏùºÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî
                  </span>
                  <input
                    type="file"
                    className="hidden"
                    accept="image/*"
                    onChange={handleFileSelect}
                  />
                </label>

                <p className="text-xs text-gray-400 mt-1">
                  PNG, JPG, GIF (ÏµúÎåÄ 5MB)
                </p>
              </div>
            ) : (
              // ÎØ∏Î¶¨Î≥¥Í∏∞
              <div>
                <img
                  src={preview}
                  alt="ÎØ∏Î¶¨Î≥¥Í∏∞"
                  className="max-h-64 mx-auto rounded"
                />

                {selectedFile && (
                  <div className="mt-4 text-sm text-gray-600 text-center">
                    <p>ÌååÏùºÎ™Ö: {selectedFile.name}</p>
                    <p>ÌÅ¨Í∏∞: {(selectedFile.size / 1024).toFixed(2)} KB</p>
                  </div>
                )}

                <div className="mt-4 flex gap-2 justify-center">
                  <button
                    type="button"
                    onClick={handleReset}
                    className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
                  >
                    Ïù¥ÎØ∏ÏßÄ Ï†úÍ±∞
                  </button>
                </div>
              </div>
            )}
          </div>

          {/* Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú ÏóêÎü¨ */}
          {error && (
            <div className="mt-2 p-3 bg-red-100 text-red-700 rounded text-sm">
              {error}
            </div>
          )}

          {/* ÏóÖÎ°úÎìú ÏôÑÎ£å */}
          {uploadedUrl && (
            <div className="mt-2 p-4 bg-green-100 rounded">
              <p className="text-green-700 font-medium mb-2">ÏóÖÎ°úÎìú ÏôÑÎ£å!</p>
              <img
                src={uploadedUrl}
                alt="ÏóÖÎ°úÎìúÎêú Ïù¥ÎØ∏ÏßÄ"
                className="mt-2 max-h-32 rounded"
              />
            </div>
          )}

          {/* ÏóÖÎ°úÎìú ÏßÑÌñâ ÏÉÅÌÉú */}
          {uploading && (
            <div className="mt-2">
              <div className="bg-gray-200 rounded-full h-2">
                <div
                  className="bg-blue-500 h-2 rounded-full animate-pulse"
                  style={{ width: "70%" }}
                ></div>
              </div>
              <p className="text-sm text-gray-600 mt-2 text-center">
                Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë...
              </p>
            </div>
          )}
        </div>

        {/* ÎÇ¥Ïö© */}
        <div>
          <label className="block text-sm font-medium mb-2">ÎÇ¥Ïö©</label>
          <textarea
            name="content"
            rows={10}
            className="w-full px-4 py-2 border rounded-lg"
            required
          />
        </div>

        {/* Ìèº ÏóêÎü¨ Î©îÏãúÏßÄ */}
        {formError && (
          <div className="text-red-600 text-sm bg-red-50 p-3 rounded">
            {formError}
          </div>
        )}

        {/* Ï†úÏ∂ú Î≤ÑÌäº */}
        <button
          type="submit"
          disabled={uploading || isPending}
          className="w-full bg-blue-600 text-white py-3 rounded-lg hover:bg-blue-700 font-medium disabled:bg-gray-400 disabled:cursor-not-allowed"
        >
          {uploading
            ? "Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë..."
            : isPending
            ? "ÏûëÏÑ± Ï§ë..."
            : "ÏûëÏÑ±ÌïòÍ∏∞"}
        </button>
      </form>
    </div>
  );
}
